<template>
  <div class="cell-container">
    <div>{{title}}</div>
    <div>{{content}}</div>
    <div v-if="showBorder" class="border-line"></div>
  </div>
</template>

<script>
  export default {
    name: 'cell',
    props: ['title', 'content', 'showBorder'],
  };
</script>

<style lang="less" scoped>
  @import "../../style/common";
  .cell-container {
    position: relative;
    .wh(7.02,0.88);
    .margin(0,0.24);
    .flx-x-between;
    .text-color1;
    .fontSize-four;
  }

  .border {
    -webkit-transform:scale(0.5);
    transform:scale(0.5);
    position:absolute;
    border-bottom:1px solid @line-color;
    bottom: 0;
    left: -50%;
    right: -50%;
  }


</style>



